import React from 'react';
import { Link, NavLink } from 'react-router-dom';
import './Header.css';

class Header extends React.Component {
    render(){
        return (
            <header id="header">
                <p className="ci"><NavLink activeClassName="active" exact to={"/"}>saramin</NavLink></p>
                <ul className="nav">
                    <li><NavLink activeClassName="active" to={"/hr"}>HR</NavLink></li>
                    <li><NavLink activeClassName="active" to={"/service"}></NavLink></li>
                    <li><NavLink activeClassName="active" to={"/ir"}>IR</NavLink></li>
                    <li><NavLink activeClassName="active" to={"/pr"}></NavLink></li>
                    <li><NavLink activeClassName="active" to={"/recruit"}></NavLink></li>
                </ul>
                <ul className="util_option">
                    <li><NavLink to={"/sitemap"}>SITEMAP</NavLink></li>
                    <li><NavLink to={"/en/"}>ENGLISH</NavLink></li>
                </ul>
            </header>
        );
    }
}

export default Header;